<template>
  <div class="module-type8">

    <section>
      <div class="dubbing">
        <div
          class="character"
          v-for="it in moduleObj.albumList"
          :key="it.programId"
          @click="getDetail(it.programId)"
        >
          <img :src="it.programImg" />
          <p>{{ it.programTit }}</p>
          <p>{{ it.programNum }}</p>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  name: "ModuleType8",
  props: {
    moduleObj: Object,
  },
  methods:{
    getDetail(id){
      this.$router.push({
        path:'/type8-item',
        query:{
          id:id
        }
      })
    }
  }
};
</script>
<style lang="scss" scoped>
